*, ::before, ::after {
  box-sizing: border-box;
}

p-slide {
  position: relative;
}

pre p-fragment {
  opacity: .2;

  &[aria-hidden="true"] p-fragment,
  &[aria-hidden="false"] {
    opacity: 1;
  }

  code[class*="language-"] {
    white-space: pre;
  }
}

:not(pre) > code {
  background-color: rgba(255, 102, 0, .1);
  border-radius: .25em;
  padding: 0 .25em;
  box-shadow: inset 0 0 .05em rgba(black, .25);

  pre & {
    box-shadow: none;
  }
}

p {
  margin: 0 0 .5em;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

pre[class*="language-"] {
  font-size: 50%;
}
pre.big[class*="language-"] {
  font-size: 75%;
  padding: .5em .6667em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
